/**
 * Layout component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React from "react"
import PropTypes from "prop-types"
import { useStaticQuery, graphql } from "gatsby"

import Header from "./header"
import "./layout.css"
import "bootstrap/dist/css/bootstrap.min.css"

const Layout = ({ children }) => {
  const data = useStaticQuery(graphql`
    {
      cosmicjsGlobals(slug: { eq: "header" }) {
        metadata {
          title
        }
      }
    }
  `)

  return (
    <>
      <Header siteTitle={data.cosmicjsGlobals.metadata.title} />
      <div>
        <main>{children}</main>
        <footer className='container-fluid p-4 d-flex flex-row'>
          {new Date().getFullYear()}, Built by&nbsp;
          {` `}
          <a style={{flex: '3', textDecoration: 'none'}} href="https://goshua13.github.io/Portfolio2/#/">Goshua13</a>
          <div style={{flex: '1;'}}><a href="https://cosmicjs.com/add-bucket?import_bucket=5dc8bef1cabf3000089cbdfe"><img src="https://cdn.cosmicjs.com/51fe54d0-4f6e-11e9-9f32-8d001da69630-powered-by-cosmicjs.svg" /></a></div>
        </footer>
      </div>
    </>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
